.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.watch__center {
  background-color: #f6a603;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: relative;
}

.watch__minutes {
  transform-origin: bottom;
  z-index: -2;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: watch-minutes;
  animation-duration: 3600s;
  animation-timing-function: step-start;
  animation-iteration-count: infinite;
  animation-delay: 60s;
  position: absolute;
  transform: translateY(-50%)rotateZ(0);
}

.watch__seconds {
  transform-origin: bottom;
  z-index: -1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: watch-seconds;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  transform: translateY(-50%)rotateZ(0);
}

.watch--speed-up .watch__minutes {
  animation-duration: 600s;
  animation-delay: 10s;
}

.watch--speed-up .watch__seconds {
  animation-duration: 10s;
}

@keyframes watch-seconds {
  0% {
    transform: translateY(-50%)rotateZ(0);
  }

  100% {
    transform: translateY(-50%)rotateZ(360deg);
  }
}

body {
  margin: 0;
